@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}



body{background-image:url(/jacoberick.com/static/media/webmain_red.efbfc100.jpg);background-size:cover;background-position:center;background-repeat:no-repeat;height:100vh;position:relative}body .main--nav{display:flex;align-items:center;justify-content:space-around;flex-direction:row;flex-wrap:wrap}body .main--nav .half-nav{width:40%;text-align:center;display:flex;justify-content:space-around}body .main--nav .half-nav .nav--link{font-family:'Raleway', sans-serif;color:#fff;padding-top:14px;font-size:2rem;text-shadow:-2px 2px 4px #121212;text-decoration:none}body .main--nav .half-nav .nav--link:hover{font-style:italic}body .main--nav .middle-nav #skellygif{height:100px}body #nameTitle{font-family:'Roboto Mono', monospace;font-size:7rem;color:#ffffff;text-shadow:0px 0px 75px #121212;position:absolute;top:30%;left:calc(50%);transform:translate(-50%);white-space:nowrap}@media only screen and (max-width: 500px){body #nameTitle{font-size:500%;left:50%;white-space:normal}}body #contactText{position:absolute;top:25%;left:50%;transform:translate(-50%)}body #contactText a{color:#121212}@media only screen and (max-width: 500px){body #contactText a,body #contactText h1{font-size:25%}}body #contactText .display-3{margin-top:0}body .music{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:100%}body .music .music--icons{display:flex;align-items:center;justify-content:space-between;flex-direction:row;flex-wrap:wrap;font-size:5rem;max-width:50rem;margin:0 auto}body .music .music--icons a{color:#121212}body .music .music--icons .grow{transition:all .2s ease-in-out}body .music .music--icons .grow:hover{transform:scale(1.5)}body .footer{position:absolute;bottom:1.1rem;width:100%}body .footer .footer--icons{font-size:2.5rem;display:flex;justify-content:space-between;max-width:35rem;margin:0 auto}@media only screen and (max-width: 500px){body .footer .footer--icons{padding:0 15px}}body .footer .footer--icons a{color:#fff}body .footer .footer--icons a:hover{color:#121212}

